  .office-title {
	text-align: left;
	margin-bottom: 2rem;
	color: #333;
  }
  
  .office-grid {
	display: grid;
	grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
	gap: 2rem;
  }
  
  .office-card {
	background: white;
	border-radius: 10px;
	padding: 2rem;
	box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
	transition: transform 0.3s ease;
  }
  
  .office-card:hover {
	transform: translateY(-5px);
  }
  
  .office-icon {
	text-align: left;
	font-size: 2rem;
	color: #4a90e2;
	margin-bottom: 1rem;
  }
  
  .office-card h3 {
	text-align: left;
	color: #333;
	margin-bottom: 1.5rem;
  }
  
  .address-details {
	margin-bottom: 1.5rem;
  }
  
  .address-details p {
	margin: 0.5rem 0;
	color: #666;
	font-family: Poppins;
  }
  
  .contact-info {
	border-top: 1px solid #eee;
	padding-top: 1rem;
  }
  
  .contact-info p {
	display: flex;
	align-items: center;
	gap: 0.5rem;
	color: #666;
	margin: 0.5rem 0;
  }
  
  .contact-info i {
	color: #4a90e2;
  }
  
  @media (max-width: 768px) {
	.office-grid {
	  grid-template-columns: 1fr;
	}
  }
  .list-none{
	list-style: none!important;
	padding-left: 0;
  }
  .list-none a {
    color: #505050;
}
.office-grid{
	text-align: left;
}